.tutorial-detail-page {
  padding: 0 8px;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

.tutorial-title {
  font-family: serif;
}
.tutorial-content {
  font-family: serif;
  white-space: pre-line;
}

.feedback {
  display: flex;
  margin: 16px 0;
}
.feedback span {
  margin-right: 16px;
}
.feedback span :hover {
  color: firebrick;
}

.comment-item {
  padding: 8px;
  border-top: 1px solid #ccc;
}

.comment-form {
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
}
.comment-textbox {
  flex: 1;
  margin-right: 10px;
}
